.skills {
  padding: 50px 0px;
  background-color: rgb(26, 30, 35);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.progress-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Circular */
.circular-progress-wrapper {
  padding: 40px 0px 0px 0px;
  margin: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.indiv-circle-container {
  text-align: center;
  padding: 10px 20px;
  width: 50vw;
  /* padding: 20px; */
}

.circular-name {
  padding: 0px;
  font-family: "Raleway";
  font-size: 16px;
  color: white;
}

@media only screen and (min-width: 768px) {
  .progress-wrapper {
    width: 90%;
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  /* Circular */
  .circular-progress-wrapper {
    width: 90%;
    padding: 20px 0px 0px 0px;
    margin: 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }

  .indiv-circle-container {
    padding: 0px;
    margin: 0px;
    text-align: center;
    width: 25%;
  }

  .indiv-circle-wrapper {
    padding: 20px;
  }

  .circular-name {
    font-size: 13px;
  }
}

@media only screen and (min-width: 1000px) {
  .progress-wrapper {
    width: 80%;
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  /* Circular */
  .circular-progress-wrapper {
    width: 80%;
    padding: 20px 0px 0px 0px;
    margin: 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }

  .indiv-circle-container {
    padding: 0px;
    margin: 0px;
    text-align: center;
    width: 20%;
  }

  .indiv-circle-wrapper {
    padding: 20px;
  }

  .circular-name {
    font-size: 17px;
  }
}
