.section-header {
  padding: 20px 0px 50px 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.section-header-text {
  font-size: 2em;
  padding: 20px 0px 40px 0px;
  text-decoration: none;
}

@media only screen and (min-width: 768px) {
  .section-header-text {
    font-size: 3em;
    padding: 20px 0px 40px 0px;
    text-decoration: none;
  }
}
